import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const state = {
  shade: {
    bottom: true,
    global: false
  },
  slide: {
    show: true
  },
  dialog: {
    repairStatus: false,
    reqairType: 'test'
  },
  footerControl: {
    show: true
  }
}

const mutations = {
  shade (state,val) {
    state.shade.bottom = val
  },
  changeSlide (state,val) {
    state.slide.show = val
  },
  dialog (state, v) {
    state.dialog[''+v.name] = v.val
  },
  changeFooter (state, v) {
    state.footerControl[''+v.name] = v.val
  }
}
const actions = {

}
const getters = {
  bShade (state) {
    return state.shade.bottom
  },
  slideStatus (state) {
    return state.slide.show
  },
  repairStatus (state) {
    return state.dialog.repairStatus
  },
  reqairType (state) {
    return state.dialog.reqairType
  },
  footerControl () {
    return state.footerControl.show
  }
}
export default new Vuex.Store({
  state,
  getters,
  actions,
  mutations
})



