<template>
    <div class="home-left-panel">
        <ul>
            <li><a href="javascript:void(0);" class="menu"></a></li>
            <li><a href="javascript:void(0);" class="user"></a></li>
            <li><a href="javascript:void(0);" class="call"></a></li>
            <li><a href="javascript:void(0);" class="exit"  v-touch:tap="loginOut"></a></li>
        </ul>
