    export default{
        data(){
            return{
                msg:'hello vue'
            }
        },
        methods: {
          loginOut () {
            this.$router.push('/login')
          }
        }
    }
</script>
<style lang="scss" scoped>
    .home-left-panel {
        width: 50px;height:100%;
        position: relative;
        z-index:21;
        background: url("../assets/home/l_bg_01.png") no-repeat 0 0 / 100% 100%;
        display:-webkit-flex;
        -webkit-align-items:center;
        ul{
            height:80%;
            display:-webkit-flex;
            -webkit-flex-direction: column;
            -webkit-justify-content: space-around;
        }
        a{
            width:50px;height:50px;
            display: block;
        }
        .menu {
            background: url("../assets/home/welcome_02.png") no-repeat 40% center;
        }
        .user{
            background: url("../assets/home/welcome_08.png") no-repeat center center;
        }
        .call{
            background: url("../assets/home/welcome_10.png") no-repeat center center;
        }
        .exit{
            background: url("../assets/home/welcome_13.png") no-repeat center center;
        }
    }
</style>



