      tabControl: 1, //  01
      repairStatus: false, // 
      activityStatus: false, // 
      showList:false
    }
  },
  computed: {
    ...mapGetters({slideStatus:'slideStatus',reqairType:'reqairType'})
  },
  methods: {
    enter() {
      this.showList = true
      this.activityStatus = true
    },
    closeSlide () {
      this.$store.commit('changeSlide',false)
    },
    openSlide () {
      this.$store.commit('changeSlide',true)
    },
    openRepair (type) {
      this.$store.commit('dialog',{
        name:'reqairType',
        val : type
      })
      this.$store.commit('dialog',{
        name:'repairStatus',
        val :true
      })
    }
  }
}
