<template>
    <div class="main">
        <!---->
        <left></left>
        <!---->
        <div class="home-right-panel" >
            <!---->
            <top></top>
            <div class="mainContent">
                <!---->
                <transition enter-active-class="slideInRight animated" leave-active-class="slideOutLeft animated">
                    <router-view class="view "></router-view>
                </transition>
                <right></right>
            </div>
            <!---->
            <bottom :class="{hideControl:!footerControl}"></bottom>

        </div>
        <!---->
      <!--  -->
        <mu-dialog dialogClass="repair" :open="repairStatus" title="Dialog" >
          <repair :class="reqairType"></repair>
          <div class="rt">
            <button v-touch:tap="reqairHistory" :class="reqairType!='onlyHistory'?'':'hide'" class="list"></button>
            <button v-touch:tap="reqairBack" :class="reqairType=='onlyHistory'?'':'hide'" class="back"></button>
            <button class="close" v-touch:tap="repairClose"></button>
          </div>
        </mu-dialog>
        <div class="bShade" :class="{hidden:!bShade}" v-touch:tap="shadeClose"></div>
    </div>
</template>
<script>
    import { mapGetters } from 'vuex'
    import top from './top.vue'
    import left from './left.vue'
    import bottom from './bottom.vue'
    import right from './right.vue'
    import repair from './repair.vue'
    export default{
        components: {
            top: top,
            left: left,
            bottom: bottom,
            right: right,
            repair:repair
        },
        data(){
            return{

            }
        },
        computed: {
          ...mapGetters({bShade:'bShade',repairStatus:'repairStatus',reqairType:'reqairType',footerControl:'footerControl'})
        },
        methods: {
          shadeClose () {
            this.$store.commit('shade',false)
          },
          repairClose () {
            this.$store.commit('dialog',{
              name:'repairStatus',
              val :false
          })
            this.shadeClose()
          },
          reqairBack () {
            this.$store.commit('dialog',{
              name:'reqairType',
              val : 'onlyApply'
            })
          },
          reqairHistory () {
            this.$store.commit('dialog',{
              name:'reqairType',
              val : 'onlyHistory'
            })
          }
        }

    }
</script>
<style lang="scss" scoped>
    .main{
        width:100%;height:100%;
        display: -webkit-flex;
        -webkit-flex-direction: row;
    }

    .home-right-panel{
        -webkit-flex-grow: 2;
        display: -webkit-flex;
        -webkit-flex-direction: column;
        position:relative;

        .mainContent{
            -webkit-flex-grow: 2;
            position: relative;
        }
    }
    .bShade {
        position: absolute;
        z-index:19;
        top: 0;
        left:0;
        width:100%;
        height:100%;
        background-color: transparent;
        opacity:.5;
    }
    .rt{
      position:absolute;top:10px;right:25px;
      button{
        border:0;
        vertical-align: middle;
        margin-left:35px;
      }
      .close{
        background: url("../assets/home/close.png") center center /100% 100%;
        width:22px;height:22px;
      }
      .list {
        background: url("../assets/home/list.png") center center / 100% 100%;
        width:17px; height:14px;
      }
     .back{
       background: url("../assets/back.png") center center / 100% 100%;
       width:22px; height:22px;
     }
    }
</style>



