<template>
    <div class="loginRegister" :class="{register:!login}">
        <ul class="login">
            <li class="account mt10"><input :value="account" type="text"></li>
            <li class="password mt10"><input :value="password" type="password"></li>
            <li class="tips mt10">Forgot your password?</li>
            <li class="mt10" v-touch:tap="_login">
                <mu-raised-button label="Sign in" class="submit button" />
            </li>
        </ul>
        <ul class="register">
            <li class="account mt10"><input :value="account" type="text"></li>
            <li class="password mt10"><input :value="password" type="password"></li>
            <li class="phone mt10"><input type="text"></li>
            <li class="mt10" >
                <mu-raised-button label="Register" class="submit button" />
            </li>
        </ul>
        <div class="b clearfix">
            <span class="f_l" v-touch:tap="changeType">Sign up</span>
            <span class="f_r">Help</span>
        </div>
    </div>
</template>
<script>
    export default{
        data(){
            return{
                    account: '',
                    password: '',
                    login: true,
                }
            },
        methods: {
            changeType () {
                this.login = !this.login
            },
            _login () {
                this.$router.push('/main/home')
            },
            inputTrue () {
                if (this.account != '' && this.password != ''){

                }
            }

        }
    }
</script>
<style lang="scss" scoped>
.loginRegister {
        background: url("../assets/login/home_bg.png") no-repeat 0 0 / 100% 100%;
        display: -webkit-flex;
        -webkit-justify-content: center;
        -webkit-align-items: center;
        width: 100%;
        height: 100%;
        font-size: 18px;
    .register{
        display: none;
    }
    &.register {
        .register {
            display: block;
        }
        .login {
            display: none;
        }
    }
    li{
        width:240px;height:45px;
        input{
            background-color: transparent;
            border:0;width:100%;height:100%;color:#fff;
            font-size:18px;
            padding: 6px 0 5px 50px;
        }
    }
    .account{
        background: url('../assets/login/load_03.png') no-repeat;
        -webkit-animation: fadeInUp .7s ease;
    }
    .password{
        background: url('../assets/login/load_06.png') no-repeat;
        -webkit-animation: fadeInUp .9s ease;
    }
    .tips{
        height:auto;
        text-align: center;
        color:#fff;
        font-size:14px;
        -webkit-animation: fadeInUp 1.1s ease;
    }
    .email{
        background: url('../assets/login/login_10.png') no-repeat;
        -webkit-animation: fadeInUp .7s ease;
    }

    .phone{
        background: url('../assets/login/login_10.png') no-repeat;
        -webkit-animation: fadeInUp 1.1s ease;
    }

    .submit{
        width:100%;
        height:100%;
        border:0;
        color:#fff;
        font-size:18px;
        text-transform: none;
        background: url('../assets/login/load_08.png') no-repeat;
        -webkit-animation: fadeInUp 1.3s ease;
    }
    .b{
        position: absolute;
        width:280px;bottom:50px;left:50%;
        margin-left:-140px;
        color:#00d5c3;
        .f_l {
            background: url('../assets/login/load_11.png') no-repeat left center;
            -webkit-transition: all 0.5s ease;
            -webkit-animation: all 0.5s ease;
            padding-left:20px;
        }
    }
}
</style>



