<template>
    <div class="bottom" :class="{ac:!bShade}">
        <ul class="control">
            <li class="goHome"></li>
            <li class="leaveHome"></li>
            <li class="lift"></li>
            <li class="unlocking"></li>
            <li class="monitor"></li>
            <li class="infrared"></li>
        </ul>
        <ul class="nav">
            <router-link to="/main/home" tag="li"><i></i></router-link>
            <router-link to="/main/society" tag="li"><i></i></router-link>
            <router-link to="/main/life" tag="li"><i></i></router-link>
            <router-link to="/main/neighbor" tag="li"><i></i></router-link>
        </ul>
        <div class="bStatus" v-touch:tap="shadeShow">
            <i></i>
        </div>
    </div>
</template>
<script>
    import { mapGetters } from 'vuex'
    export default{
        data(){
            return{
                msg:'hello vue'
            }
        },
        computed: {
            ...mapGetters({bShade:'bShade'})
        },
        methods: {
          shadeShow () {
            this.$store.commit('shade', true)
          }
        }
    }
</script>
<style lang="scss" scoped>
    .hideControl{
      &.bottom.ac{
        pointer-events: none;
      }
      .control{
        display: none;
      }
    }
    .bottom{
        position: absolute;
        left:0;bottom:0;
        height:128px;
        width:100%;
        z-index:20;
        &.ac{
            .control{
                opacity: 1;visibility: visible;
            }
            .nav{
                background-color: transparent;
                li{
                    -webkit-transform: scale(0,0);
                }
            }
            .bStatus{
                -webkit-transform: scale(1);
            }
        }
    }
    .nav{
        background: rgba(39,39,40,.5);width:100%;height:100%;
        display: -webkit-flex;-webkit-justify-content: center;
        -webkit-align-items:center;
        .router-link-active{
            i{
                -webkit-animation-play-state: running;
            }
        }
        li{
            height:96px;width:96px;border-radius:50%;
            margin:0 30px;color:#fff;-webkit-font-smoothing: antialiased;
            -webkit-transition: transform .5s ease;
            font-size:22px;
            position: relative;
            line-height:96px;
            text-align: center;
            i {
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                border-radius: 50%;
                background: url("../assets/home/bC1.png") no-repeat 0 0 / 100% 100%;
                -webkit-animation: rotate 3s linear infinite forwards;
                -webkit-animation-play-state: paused;
                &:nth-child(2){
                    background-image: url("../assets/home/bC2.png");
                }
                &:nth-child(3){
                    background-image: url("../assets/home/bC3.png");
                }
                &:nth-child(4){
                    background-image: url("../assets/home/bC4.png");
                }
            }

        }
    }
    .control {
        opacity: 0;
        visibility: hidden;
        transition: all .5s ease;
        -webkit-transition: all .5s ease;
        z-index: 100;
        width: 552px;
        background: url('../assets/home/Background.png');
        height: 63px;
        position: fixed;
        left: 50%;
        margin-left: -276px;
        bottom: 40px;
        box-sizing: border-box;
        text-align: center;
        display: -webkit-flex;
        -webkit-justify-content: center;
       li {
           -webkit-font-smoothing: antialiased;
           line-height: 150px;
           font-size: 10px;
           width: 40px;
           margin: 0 10px;
           color: #fff;
           background: url("../assets/home/bControl.png") no-repeat -11px -69px;
           &:hover{
                background-position-y: 2px;
            }
            &:nth-child(2){
                 background-position-x: -72px;
             }
            &:nth-child(3){
                 background-position-x: -133px;
             }
            &:nth-child(4){
                   background-position-x: -187px;
           }
            &:nth-child(5){
                 background-position-x: -244px;
             }
            &:nth-child(6){
                 background-position-x: -305px;
             }
       }
    }
    .bStatus{
        pointer-events: all;
        position: absolute;
        border-radius:50%;
        left:15px;
        bottom:15px;
        width:50px;
        height:50px;
        border:5px solid #9f9f9f;
        background-color:rgba(39, 39, 40, 0.5);
        -webkit-transform: scale(0);
        -webkit-transition:all .4s ease;
        i{
            position: absolute;
            left:2px;
            top:2px;
            width:36px;
            height:36px;
            border:1px solid #ccc;
            border-radius:50%;
            clip:rect(0px,30px,36px,0px);
            -webkit-animation: rotate 2s linear infinite forwards;
        }
        &:after{
            content:' ';
            position: absolute;
            top: -5px;
            left: -5px;
            width: 50px;
            height:50px;
            border:5px solid #188f8a;
            border-radius:50%;
             clip:rect(0px,40px,50px,0px);
             -webkit-animation: rotate 2s linear infinite forwards;
        }
    }
</style>



