<template>
  <div id="society" class="home-content view-container">
    <div class="mainContent pane society-group" >
      <div class="society-map">
        <ul class="camera">
          <li v-for="v,k in societyView" :class="{'active':v.show}">
            <button class="look" v-touch:tap="viewEnter.bind(this,k)"></button>
            <div class="box" >
              <p></p>
              <img :src="'static/Background'+(k+1)+'.png'">
              <span>{{k+1}}</span>
              <div class="btnL">
                <button v-touch:tap="showMonitoring.bind(this,k)"></button>
                <button v-touch:tap="()=>{v.show=false}"></button>
              </div>
            </div>
          </li>
        </ul>
        <ul class="man">
          <li v-on:webkitAnimationEnd="tadaing=false" class="animated" :class="{'tada': tadaing}">
            <div class="card">
              <p class="name"></p>
              <span></span>
            </div>
          </li>
        </ul>
      </div>
      <div class="society-items" :style="slideStatus?'filter:blur(5px)':''">
        <h2></h2>
        <div class="gprs">
          
        </div>
        <ul>
          <li><img src="static/test.png"><p></p></li>
        </ul>
        <div class="shake" v-touch:tap="tada"></div>
        <textarea placeholder=""></textarea>
        <button></button>
      </div>
    </div>
    <!--  -->
    <mu-dialog dialogClass="monitoring" :open="monitoringStatus" title="Dialog" >
      <monitoring></monitoring>
      <div class="close" v-touch:tap="()=>{monitoringStatus=false}"></div>
    </mu-dialog>
  </div>
</template>
<script>
  import { mapActions, mapGetters } from 'vuex'
  import monitoring from './monitoring.vue'
  export default{
    created () {
      this.$store.commit('changeFooter',{
        name:'show',
        val:false
      })
    },
    components: {
      monitoring: monitoring
    },
    computed: {
      ...mapGetters({slideStatus:'slideStatus'})
    },
    data () {
      return {
        monitoringName: '1',
        monitoringStatus: false,
        tadaing: false,
        societyView: [
          {show:false},{show:false},{show:false},{show:false}
        ]
      }
    },
    methods: {
      viewEnter (k) {
        for(var i= 0;i<this.societyView.length;i++){
          this.societyView[i].show=false
        }
        this.societyView[k].show=true;
      },
      showMonitoring (index) {
        this.monitoringName = index
        this.monitoringStatus = true
      },
      tada () {
        this.tadaing = true
      }
    }
  }
</script>
<style lang="scss">
  .mu-dialog.monitoring{
    width:808px;height:456px;max-wdith:none;
    .mu-dialog-title{
      display: none !important;
    }
    .mu-dialog-body{
      height:100%;padding:0;
    }
  }
</style>
<style lang="scss" scoped>
  .close{
    background: url("../assets/home/close.png") no-repeat center center /21px 21px;
    width:21px; height:21px;
    position: absolute;
    right:20px;top:10px;
  }
  .society-group {
    display: -webkit-flex;
    width: 100%;padding:18px 21px 37px 24px;
    height: 100%;
  }
  .society-map {
    position: relative;
    border-radius:5px 0 0 5px;
    -webkit-flex-grow:10;
    height: 100%;
    background: url("../assets/home/shequMap.png") no-repeat;
    background-size: 100% 101%;
    left: 0;
  .man{
  li{
    background: url("../assets/home/redCircle.png") no-repeat;
    width:22px;height:22px;
    position: absolute;
    top:35%;left:56%;
    -webkit-animation-duration: 1s;
  .card {
    background: url("../assets/home/Box1.png") no-repeat;
    width:165px;height:97px;
    display: -webkit-flex;
    -webkit-flex-direction: column;
    -webkit-align-items: center;
    -webkit-justify-content: center;
    padding:0 6px 28px 6px;
    color:#6d6d6d;
    position: absolute;
    left: 9px;
    top: 10px;
    -webkit-transform: translate(-50%,-100%);
  p {
    border-bottom: 1px solid #c7c7c7;
    width:100%;
    font-size:12px;
    text-align: center;
    padding-bottom:8px;
  }
  span {
    display: block;
    font-size: 9px;
    padding-top: 5px;
    letter-spacing: 2px;
    margin-left:2px;
  }
  }
  }
  }
  .camera{
  li {
    position: absolute;
    color: #fff;
    text-align: center;
    .look{
      background: url('../assets/home/camera.png') no-repeat;
      width: 44px;
      height: 50px;
      text-indent: -14px;border:0;
      line-height: 40px;
    }
    &.active{
      .box{
        -webkit-transform: scale(1);
     }
    }
  .box {
    display: -webkit-flex;
    z-index:2;
    -webkit-flex-direction: column;
    background: url("../assets/home/Box.png") no-repeat -5px top;
    width: 181px;
    height: 183px;
    color: #333;
    padding: 0 14px;
    text-align: left;
    text-indent: 0;
    padding-top: 15px;
    position: absolute;
    top: 100%;
    margin-left:-90.5px;
    -webkit-transform: scale(0);
    -webkit-transition: transform .2s ease;
    left:14px;
  p {
    font-size: 12px;
    width: 100%;
    height: 30px;
    border-bottom: 1px solid #c7c7c7;
    margin-bottom: 8px;
    line-height: 34px;
  }
  img {
    border: 1px solid #c7c7c7;
    height: 50px;
  }
  span {
    font-size: 8px;
    line-height: 14px;
  }
  .btnL {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
  }
  button {
    font-size: 10px;
    width: 70px;
    height: 20px;
    border-radius: 5px;
    border: 1px solid #c7c7c7;
    color: #6d6d6d;
    background: -webkit-gradient(linear, center top, center bottom, from(#fff), to(#d3d3d3));
    letter-spacing: 2px;
  }
  }
  &:nth-child(1) {
     top: 7%;
     left: 20%;
   }
  &:nth-child(2) {
     top: 6%;
     left: 74%;
   }
  &:nth-child(3) {
     top: 23%;
     left: 87%;
   }
  &:nth-child(4) {
     top: 51%;
     left: 56%;
   }
  &:nth-child(5) {
     top: 70%;
     left: 27%;
   }
  }
  }
  }
  .society-items{
    border-radius:0 5px 5px 0;
    background-color: #fff;
    width:248px;padding:0 32px;
    display:-webkit-flex;
    -webkit-flex-direction: column;
    -webkit-justify-content: center;
    font-size:10px;
    text-align: center;
  h2{
    text-align: center;color:#000;
    font-size:24px;
  }
  .gprs {
    background: url("../assets/home/Sent Icon.png") no-repeat top center;
    padding-top:26px;
    margin:6px 0px 10px 0px;
    color:#a8acb9;
  }
  .camera {
    display: -webkit-flex;
    -webkit-justify-content: space-around;
  }
  li{
    background-color: #fff;color:#a8acb9;
  img{
    width:52px;
    height:52px;
    padding:1px;
    border: 1px solid #5f6060;
    -webkit-border-radius:50%;
    -moz-border-radius:50%;
    border-radius:50%;
  }
  }
  .shake{
    height:24px;
    line-height:24px;
    width:100%;
    color:#333;
    letter-spacing: 1px;
    border-radius: 20px;
    box-shadow: 0px 0px 10px 2px #ccc;
    background-color: #fff;
    margin-top:42px;
  }
  textarea{
    background-color:#eff0f4;
    resize: none;
    border:none;
    height:94px;
    padding:5px 10px;
    -webkit-border-radius:5px;
    -moz-border-radius:5px;
    font-size:12px;
    border-radius:5px;
    text-align: left;
    line-height:24px;
    margin:33px 0 30px 0;
    color:#000;
  }
  textarea::-webkit-input-placeholder {
    color:#a8acb9;
  }
  button{
    border:0;color:#fff;
    background-color: #00d5c3;
    width:80px;height:26px;
    font-size:12px;
    border-radius: 20px;
    letter-spacing: 4px;
    margin:0 auto;
  }
  }
</style>



