          </div>
        </div>
        <!--<ul class="else">-->
            <!--<li class="message"><i>3</i></li>-->
            <!--<li class="video"></li>-->
            <!--<li class="connect"></li>-->
            <!--<li class="wifi"></li>-->
            <!--<li class="skin"></li>-->
        <!--</ul>-->
      <div class="top-icon">
        <div on-tap="iconChange(1)" class="message"><i>2</i><div style="left:0;" class="content"><img src="static/img/infoCard.png" alt=""><div class="more"><div class="ac"></div><img src="static/img/infoDetails.png" alt=""></div></div></div>
        <div on-tap="iconChange(2)" class="video"><div class="content" style="z-index:99"><img src="static/img/video.png" alt=""><div class="addressBook" style="z-index:99"><img  src="static/img/video1.png" alt=""></div></div></div>
        <div on-tap="iconChange(3)"><div class="content"></div></div>
        <div on-tap="iconChange(4)"><div class="content"></div></div>
        <div on-tap="iconChange(5)"><div class="content"></div></div>
        <!--ng-class="{true:'on',false:''}[icon_on==5]"-->
      </div>
      <div class="top-music">
        <audio v-on:loadedmetadata="loadedmetadata()" v-on:timeupdate="timeupdate(true)" id="media" >
          <source src="static/music/ - .mp3" controls="" />
        </audio>
        <div class="t">
          <div class="mControl">
            <p class="musicTitle"> - </p>
            <div class="playTime">
              <span>{{currentTime|minutes}}</span><mu-slider :min="0" :max="Number(duration)" v-on:change="timeupdate(false)" v-model="currentTime" class="demo-slider"/><span class="duration">{{duration|minutes}}</span>
            </div>
            <div class="playControl">
              <span class="prev"></span><span v-touch:tap="play" class="play"></span><span class="next"></span>
            </div>
          </div>

        </div>
      </div>
        <ul class="weather">
            <li class="nowWeather">
                <h2>{{weatherData[0].date | fNowWeather}}</h2>
                <p>{{weatherData[0].wind}}{{weatherData[0].weather}}</p>
            </li>
            <li v-for="val,index in weatherData" v-if="index !==0">
                <p>{{val.date}}</p>
                <p class="img" :alt="index"></p>
                <p>{{val.temperature | fWeather}}</p>
            </li>
        </ul>
    </div>
</template>

<script>
    export default{
        mounted () {
            let that = this
            that.$http.get(
              'http://api.jirengu.com/weather.php',
              {emulateJSON: true, emulateHTTP: true}
            ).then((data) => {
              data.json().then(function (re) {
                that.weatherData = re.results[0].weather_data
              })
            }, (response) => {
              console.log(response)
            })
        },
        data(){
            return{
                weatherData: [{data: '', wind: ''}], // 
                dateShow: false,
                dateContent: false,
                canPlay: true, // 
                currentTime: '0', // 
                duration: '0', // 
            }
        },
        methods: {
          loadedmetadata () {
            let audio = document.getElementById('media')
            this.duration = audio.duration
          },
          play () {
            let audio = document.getElementById('media')
            audio.currentTime = this.currentTime
            if (audio.paused === true) {
              audio.play()
            } else {
              audio.pause()
            }
          },
          timeupdate (auto) {
            let audio = document.getElementById('media')
            if (auto === true && this.canPlay === true) {
              this.currentTime = audio.currentTime
            } else {
              this.canPlay = false
              var that = this
              audio.currentTime = this.currentTime
              setTimeout(function () {
                that.canPlay = true
              }, 50)
            }
          },
        },
        filters: {
          minutes (val) {
            val = val || 0
            var output = ''
            var h = (val / 60).toFixed(0)
            var s = (Number(val).toFixed(0)) % 60
            output = h + ':' + s
            return output
          },
            fWeather (val) {
            val = val || ''
            var output = ''
            output = val.replace(/ ~ /g, '/').replace(//g, '').replace(/(.*)\/(.*)/g, '$2' + '/$1')
            return output
            },
            fNowWeather (val) {
                val = val || ''
                var output = ''
                output = val.replace(/.*(.*).*/g, '$1')
                return output
            },
            time (val) {
                val = val || 0
                var output = ''
                var h = (val / 60).toFixed(0)
                var s = (Number(val).toFixed(0)) % 60
                output = h + ':' + s
                return output
            }
        }
    }
</script>
