<template>
  <div id="life" class="home-content view-container">
    <div class="mainContent pane" >
      <div class="life-img" :class="[{'active':v.show},'life-img'+(k+1)]" v-for="v,k in List">
        <div v-touch:tap="showList.bind(this,k)" class="img"></div>
        <ul>
          <li><a href="javascript:void(0)"><img src="static/2_04.png"></a></li>
        </ul>
      </div>

    </div>
  </div>
</template>
<script type="text/javascript">
  import { mapActions } from 'vuex'
  export default{
    created () {
      this.$store.commit('changeFooter',{
        name:'show',
        val:false
      })
    },
    data () {
      return {
        List:[
          {show:false},{show:false},{show:false},{show:false}
        ]
      }
    },
    methods: {
      showList (i) {
        this.List[i].show = !this.List[i].show;
      }
    }
  }
</script>
<style lang="scss" scoped>
  #life {
    width: 100%;
    min-height: 100%;overflow: auto;
    .mainContent{
      -webkit-overflow-scrolling: touch;overflow: auto;
    }
    .life-img{
      width:100%;
      .img{
        height:164px;background-size: 100% 164px;
      }
      img{
        width:100%;height:164px;
      }
      &.active ul{
        height:164px;
      }
      ul{
        height:0;overflow:hidden;transition:all .5s ease;
        li{
          height:164px;
        }
      }
    }
    .life-img1 .img{
      width: 100%;
      background-image: url('../assets/life/2_03.png');
    }
    .life-img2 .img{
      width: 100%;
      background-image: url('../assets/life/2_05.png');
    }
    .life-img3 .img{
      width: 100%;
      background-image: url('../assets/life/2_03.png');
    }
    .life-img4 .img{
      width: 100%;
      background-image: url('../assets/life/2_05.png');
    }
  }
</style>



