<template>
    <div class="monitoringBody">
      <div class="oper" :class="{'acBack':backing}">
        <div class="fixed">
          <button v-touch:tap="backOpen"></button>
          <button></button>
          <button></button>
          <div class="control">
            <button class="btn up"></button>
            <button class="left btn"></button>
            <button class="down btn"></button>
            <button class="right btn"></button>
            <button class="play"></button>
          </div>
          <button></button>
          <button></button>
          <button></button>
        </div>
        <div class="playback">
          <div class="control">
            <button class="back"></button>
            <button class="play"></button>
            <button class="go"></button>
          </div>
          <div class="slider"><mu-slider :min="0"/></div>
          <p class="time">
            <span class="currentTime">1:23</span>
            /
            <span class="duration">5:16</span>
          </p>
          <button class="sound"></button>
          <div class="back" v-touch:tap="backExit"></div>
        </div>
      </div>
    </div>
</template>
<script>
    export default{
        data(){
            return{
              backing:false
            }
        },
        methods: {
          backOpen () {
            this.backing = true
          },
          backExit () {
            this.backing = false
          }
        }

    }
</script>
<style lang="scss" scoped>
  .monitoringBody{
    width:100%;height:100%;
    background: url("../assets/Image1.png") no-repeat 0 0 /100% 100%;
  }
  .oper{
    height:92px;
    position: absolute;bottom:0px;width:100%;
    background-color: rgba(0,0,0,.6);overflow: hidden;
    &.acBack{
      .fixed{
        -webkit-transform: translateY(100%);
      }
      .playback{
        -webkit-transform: translateY(0%);
      }
    }
  }
  .fixed{
    position: absolute;bottom:0px;height:92px;width:100%;
    display:-webkit-flex;-webkit-flex-direction: row;
    -webkit-align-items: center;
    -webkit-transition: all .5s ease;
    -webkit-justify-content: center;
    button{
      height:22px;width:66px;background-color: transparent;
      border-radius:10px;color:#fff;margin:0 5px;
      font-weight:bold;
      border:0;
      font-size:9px;
      &:active,&.ac{
        color:#000;border-radius:10px;background-color: #fff;
      }
    }
    .control{
      height:86px;width:86px;background: url("../assets/bgc.png") no-repeat 0 0/100% 100%;
      border-radius:50%;position:relative;margin:0 25px;
      .play{
        width:22px;height:28px;background: url("../assets/pause.png") no-repeat center center/ 11px 14px;
        position: absolute;top:50%;left:50%;margin-left:-11px;margin-top:-14px;
        &:active{
           background-image: url("../assets/pause1.png");
         }
      }
      .btn{
        width:20px;height:20px;background: url("../assets/up.png") no-repeat center center/ 12px 7px;
        position: absolute;border-radius:50%;margin:0;
        &:active{
            background-image: url("../assets/up1.png");
         }
      }
      .up{
        left:50%;margin-left:-10px;top:1px;
      }
      .down{
        -webkit-transform: rotate(180deg);bottom:1px;left:50%;margin-left:-10px;
      }
      .left{
        -webkit-transform: rotate(-90deg);left:1px;top:50%;margin-top:-10px;
      }
      .right{
        -webkit-transform: rotate(90deg);right:1px;top:50%;margin-top:-10px;
      }
    }
  }
  .playback{
    display: -webkit-flex;width:100%;
    -webkit-flex-direction: row;
    -webkit-justify-content: center;
    -webkit-transform: translateY(-100%);
    -webkit-transition: all .5s ease;
    -webkit-align-items: center;height:100%;
    .control{
      display: -webkit-flex;
      -webkit-justify-content: center;
      -webkit-align-items: center;
    }
    button.back,button.go{
      background: url("../assets/Icon2.png") no-repeat center 11px/ 11px 7px;
      border:0;
      width: 28px;height:28px;
    }
    button.back{
      -webkit-transform: rotate(180deg);background-position-y:10px;
    }
    button.play{
      background: url("../assets/Play1.png") no-repeat 0 0/ 33px 33px;
      width:33px;height:33px;border:0;
    }
    .slider{
      width:514px;margin:0 20px 0 32px;
      display: -webkit-flex;
      -webkit-align-items: center;
      .mu-slider{
        margin-bottom:0px;
      }
    }
    .time{
      color:#b8c5c6;
      margin-right:25px;
      font-size:10px;font-style: italic;
    }
    .sound{
      background: url("../assets/Icon1.png") no-repeat center center / 16px 13px;
      width:30px;height:30px;border:0;
    }
    div.back{
      background: url("../assets/Arrow-icon.png") no-repeat center center / 16px 10px;
      width:20px;height:20px;
      position: absolute;
      top:5px;left:50%;margin-left:-10px;
    }
  }

</style>



